import React, { useState, useEffect, useRef } from "react";
import "./styles.scss";
import logo from "../../images/logo.png";
import { main_audio } from "../../utils/audios";

const Header = ({
  currentQuestion = 0,
  questions = [],
  positionChanged,
  isLast,
}) => {
  const isFirst = currentQuestion === 0;
  const [audioMuted, setAudioMuted] = useState(true);
  const audioRef = useRef(null);

  useEffect(() => {
    const audioElement = audioRef.current;
    audioElement.loop = true;

    const handleUserInteraction = () => {
      // Remove the event listener and play the audio
      document.removeEventListener("click", handleUserInteraction);
      document.removeEventListener("keydown", handleUserInteraction);
      audioElement.muted = false;
      audioElement.play();
      setAudioMuted(false);
    };

    // Add event listener for keydown event to trigger the audio playback
    document.addEventListener("click", handleUserInteraction);
    document.addEventListener("keydown", handleUserInteraction);

    return () => {
      // Clean up event listeners and stop the audio
      document.removeEventListener("click", handleUserInteraction);
      document.removeEventListener("keydown", handleUserInteraction);
      audioElement.pause();
    };
  }, []);

  const muteAudio = () => {
    const audioElement = audioRef.current;
    audioElement.muted = true;
    setAudioMuted(true);
  };

  const unmuteAudio = () => {
    const audioElement = audioRef.current;
    audioElement.muted = false;
    setAudioMuted(false);
  };

  return (
    <header className="header">
      <div className="container">
        <div className="header__wrapper">
          <div className="header__links">
            <a href="https://theqream.com/" className="header__logo">
              <span>
                check out <strong>qream</strong>
              </span>
              <img src={logo} alt="logo" />
            </a>
          </div>

          {!isLast && (
            <div
              className={`header__stage-count ${positionChanged ? "show" : ""}`}
            >
              <div className="currentQuestion">{currentQuestion + 1}</div>
              <div className="countQuestion">
                of <span>{questions.length}</span>
              </div>
            </div>
          )}

          <div className="header__mute">
            {!audioMuted ? (
              <button onClick={muteAudio}>OFF</button>
            ) : (
              <button onClick={unmuteAudio}>ON</button>
            )}
          </div>
        </div>
      </div>
      <audio ref={audioRef} src={main_audio} />
    </header>
  );
};

export default Header;
