import { useRef, useEffect, useState } from "react";
import Lottie from "lottie-react";
import box from "../../animations/box.json";
import sneakers from "../../animations/sneakers.json";
import juice from "../../animations/juice.json";
import abstract from "../../animations/abstract.json";
import abstract2 from "../../animations/abstract2.json";
import rope from "../../animations/rope.json";
import hat from "../../animations/hat.json";
import sugar from "../../animations/sugar.json";

import boxMob from "../../animations/mob/box.json";
import sneakersMob from "../../animations/mob/sneakers.json";
import juiceMob from "../../animations/mob/juice.json";
import abstractMob from "../../animations/mob/abstract.json";
import abstract2Mob from "../../animations/mob/abstract2.json";
import ropeMob from "../../animations/mob/rope.json";
import hatMob from "../../animations/mob/hat.json";
import sugarMob from "../../animations/mob/sugar.json";
import Parser from "html-react-parser";
import "./styles.scss";

import { audios } from "../../utils/audios";

const QuizAnswer = ({
  currentQuestion,
  selectedOption,
  showQuizAnswer,
  questions,
  onAnimationComplete,
  isLast,
  isCorrect,
  finish,
}) => {
  const lottieRef = useRef();
  const [animationData, setAnimationData] = useState(null);

  const animations = [
    box,
    sneakers,
    juice,
    abstract,
    sugar,
    abstract2,
    rope,
    hat,
  ];

  const animationsMob = [
    boxMob,
    sneakersMob,
    juiceMob,
    abstractMob,
    sugarMob,
    abstract2Mob,
    ropeMob,
    hatMob,
  ];

  const audio = new Audio(audios[currentQuestion]);
  const handleClick = () => {
    lottieRef.current?.play();
    audio.play();
  };

  const handleAnimationComplete = () => {
    onAnimationComplete(true);
  };

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setAnimationData(animationsMob[currentQuestion]);
      } else {
        setAnimationData(animations[currentQuestion]);
      }
    };

    handleResize();

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [currentQuestion, animations, animationsMob]);

  return (
    <div
      className={`QuizAnswer ${showQuizAnswer ? "QuizAnswer--visible" : ""}`}
    >
      <div
        className={`QuizAnswer__wrapper ${!isCorrect ? "wrong" : ""} ${
          showQuizAnswer ? "animate" : ""
        }`}
      >
        <div
          className={`${!isCorrect ? "wrong" : ""} QuizAnswer__overlay`}
        ></div>
        {isCorrect ? (
          <h2
            className={`QuizAnswer__title ${showQuizAnswer ? "animate" : ""}`}
          >
            yes, it's {selectedOption}
          </h2>
        ) : (
          <h2
            className={`QuizAnswer__title ${showQuizAnswer ? "animate" : ""}`}
          >
            nah, It's a{" "}
            {
              questions[currentQuestion].options.find(
                (option) => option.isCorrect
              ).answer
            }
          </h2>
        )}
        <div className="QuizAnswer__content">
          <div
            className={`QuizAnswer__animation ${
              showQuizAnswer ? "animate" : ""
            }`}
          >
            <Lottie
              lottieRef={lottieRef}
              animationData={animationData}
              loop={false}
              autoplay={false}
              onComplete={handleAnimationComplete}
            />
          </div>
          <div
            className={`QuizAnswer__tip  ${showQuizAnswer ? "animate" : ""}`}
          >
            <div className="QuizAnswer__tip-wrapper">
              <h3>{isCorrect ? "answer" : "correct answer "}</h3>
              <p>{questions[currentQuestion].answerTip}</p>
              <button
                className="QuizAnswer__next btn-purple"
                onClick={handleClick}
              >
                {finish ? "Finish" : Parser("Next <br /> question")}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default QuizAnswer;
